.cripto-banner-sec{
    background-color: var(--theme-blue);
    .h-logo{
        svg{
            path{
                &:nth-child(1){
                    fill:var(--white);
                }
            }
            
        }
    }
}
.crazy-header{
    .h-menu{
        >li{
            >a{
                color: var(--white);
            }
        }
    }
    .menu-trigger {
        span {
            background-color: var(--white);
        }
    }
    .mobile-menu-wrap{
        background-color: var(--theme-blue);
    }
    .menu-close{
        color: var(--white);
    }
    .mobile-menu{
        .h-menu{
            li{
                i{
                    color: var(--white);
                }
            }
            .has-child{
                a{
                    color: var(--white);
                }
            }
        }
        .mm-widget-title{
            color: var(--white);
        }
        .mm-menu{
            li{
                a{
                    color: var(--white);
                }
            }
        }
    }
}
.selectpicker{
   .nice-select{
     border-radius: 5px;  
     background:var(--primary-gradient2);
     border: none;
     color: var(--white);
     height: 35px;
     line-height: 35px;
     &:after{
         border-color: var(--white);
     }
     .list{
         left: unset;
         right: 0;
         li{
             color: var(--heading);
             margin-bottom: 0px;
             &::before{
                 display: none;
             }
         }
     }
   } 
}
@media all and (max-width: 1199px){
     .crazy-header{

        .selectpicker{
            display: none;
        }
    }
}
// tread-title-area
.tread-title-area{
    padding: 8px 0px;
    background-color: var(--theme-blue-light);
    .tread-slider-wrapper{
        p{
            margin-bottom: 0px;
            color: var(--white);
            font-size: 16px;
            font-weight: 500;
            text-transform: capitalize;
            font-family: var(--ff-secondary);
        }
    }
}
// banner style start here
.cripto-banner {
    position: relative;
    padding: 190px 0px 230px;
    z-index: 1;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.cripto-banner-text{
   max-width: 715px;
   h3{
     font-family: var(--ff-secondary);
     font-size: 30px;
     font-weight: bold;
     color: var(--white);
     margin-bottom: 5px;
     span{
         font-weight: normal;
         display: inline-block;
         margin: 0px 5px;
     } 
   } 
   h2{
     font-family: var(--ff-secondary);
     font-size: 64px;
     font-weight: normal;
     color: var(--white);
     margin-bottom: 15px;
     strong{
         font-weight: bold;
         display: inline-block;
         margin: 0px 5px;
     } 
   } 
   p{
       color: var(--white);
       font-size: 18px;
       line-height: 1.5;
       margin-bottom: 30px;
   }
   .btn{
       border-radius: 5px;
       padding: 15px 25px;
   }
}
.cripto-banner-anime{
  position: absolute;
  right: 0;
  bottom: 150px;  
  z-index: -1;
}
@keyframes zoom {
    0%{
        clip-path: circle(40% at 0 0);  
    }
    100%{
        clip-path: circle(200% at 0 0);
    }
}
.cripto-banner-counter{
    padding:45px 50px;
    background: var(--blur-white);
    border: 1px solid var(--cripto-border);
    box-sizing: border-box;
    backdrop-filter: blur(15px);
    border-radius: 10px;
    font-family: var(--ff-secondary);
    h3{
       font-size: 30px;
       font-weight: 600;
       color:var(--white); 
       line-height: 1.5;
    }
    p{
        font-size: 20px;
        font-weight: normal;
        margin-bottom: 0px;
        color: var(--white);
        line-height: 1.5;
    }
    .btn{
        margin-bottom: 40px;
        padding: 15px 25px;
        border-radius: 5px;
    }
    p{
        span{
          font-size: 14px;
          font-weight: 500;
          line-height: 1.5;
        }
    }
    ul{
        display: flex;
        align-items: center;

        li{
          padding:10px;  
          &:first-child{
              padding-left: 0px;
          }
        }
    }
}
.cripto-counter{
    margin-top:40px;
    margin-bottom: 20px;
    .countdown-clock{
        display: grid;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
        grid-gap: 30px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        margin: 0 auto;
    }
    .ci-inner{
        text-align: center;
        
        width: 70px;
        >div{
           width: 100%;
           height: 70px;
           background-color: var(--white);
           display: flex;
           align-items: center;
           justify-content: center; 
           font-size: 24px;
           color: var(--theme-blue-light);
           margin-bottom: 10px;
           font-weight: 600;
        }
        h4{
            font-size: 16px;
            color: var(--white);
            font-weight: normal;
        }
    }
}
