.cripto-about-area{
    padding: 70px 0px 100px;
}
.cripto-about-wrapper{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.cripto-about-content{
    flex: 1;
    margin-left: 60px;
    p{
        font-size: 17px;
        line-height: 1.5;
        margin-bottom: 20px;
        max-width: 700px;
    }
    ul{
        max-width: 400px;
        margin-bottom: 30px;
        li{
          position:relative;
          font-size: 15px;
          line-height: 1.5;
          font-family: var(--ff-secondary);
          margin-bottom: 10px;
          padding-left: 20px;
          font-weight: 400;
          &:after{
              position: absolute;
              content: "";
              top: 7px;
              left: 0;
              height: 8px;
              width: 8px;
              background-color: var(--theme-blue-light);
             border-radius: 50%;
          }
        }
    }
    .btn{
        border-radius: 5px;
    }
}
.cripto-heading{
    h2{
      font-family: var(--ff-secondary);  
      font-size: 30px;
      font-weight: 600;
      position: relative;
      padding: 35px 0px;
      display: inline-block;
      text-transform: capitalize;
      span{
          display: inline-block;
          padding:0px 5px;
          color: var(--theme-blue-light);
      }
      &:after{
        content: attr(data-watermark);
        position: absolute;
        left: calc(100% - 100px);
        -webkit-text-stroke: 1px var(--title-border);
        color: transparent;
        font-size: 130px;
        line-height: .9;
        white-space: nowrap;
        text-transform: uppercase;
        top: 50%;
        z-index: -1;
        transform: translateY(-50%);
      }
    }
}
@media all and (max-width: 1350px){
    .cripto-heading {
        h2{
            &:after {
                left: 0;
            }
        }
    }
    .cripto-about-area {
        padding: 70px 0px;
    }
}
@media all and (max-width: 1199px){
    .cripto-about-img {
        max-width: 45%;
    }
    .cripto-heading {
        h2{
            &:after {
                font-size: 90px;
            }
        }
    }
}
@media all and (max-width: 991px){
    .cripto-about-content {
        margin-left: 35px;
    }
}
@media all and (max-width: 767px){
    .cripto-about-wrapper {
        flex-direction: column;
    }
    .cripto-about-img {
        max-width: 500px;
        order: 2;
    }
    .cripto-about-content {
        margin-left: 0px;
        order: 1;
        margin-bottom: 50px;
    }
}

