.cripto-roadmap-area{
    padding: 60px 0px 220px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: var(--copyright-bg);
}
.cripto-roadmap-wrapper{
   .sec-title{
     max-width: 800px;
     margin: 0 auto;
     padding-bottom: 60px;
     h1{
         font-size: 30px;
         color: var(--white);
         padding: 40px 0px;
         font-family: var(--ff-secondary);
         &:before{
             -webkit-text-stroke: 1px rgba(238, 238, 238, 0.4);
             font-size: 145px;
             text-transform: uppercase;
         }
     }
     p{
         color: var(--white);
         font-size: 16px;
         line-height: 1.5;
         padding-top: 20px;
         font-family: var(--ff-secondary);
         margin: 0px 35px;
     }
   } 
}
.cripto-roadmap-content{
    margin-top: 175px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 120px;
    counter-reset: counter;
    li{
        width: calc(100% / 3 - 150px);
        box-sizing: border-box;
        display: inline-block;
        position: relative;
        height: auto;
        box-sizing: border-box;
        font-family: var(--ff-secondary);
        z-index: 1;
        transition: .4s ease-in;
        &:nth-child(even){
            margin-top: -320px;
            margin-left: -100px;
            margin-right: -100px;
            &:hover{
                transform: translateY(20px);
            }
        }
        &:nth-child(odd){
            &:hover{
                transform: translateY(-20px);
            }
        }
        &:before{
            position: absolute;
            counter-increment: counter;
            content:"0" counter(counter);
            top: -35px;
            left: 50%;
            transform: translateX(-50%);
            height: 70px;
            width: 70px;
            background-color: #0D005B;
            border: 2px solid #5D44BE ;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index:2;
            font-size: 20px;
            font-weight: 500;
            color: var(--white);
            transition: .4s ease-in-out;
        }
        &:hover{
            &:before{
                background-color: #5D44BE;
                border-color:#00B2FF;
            }
        }
        h5{
          font-size: 15px;
          line-height: 1.4;
          margin-bottom: 5px;
          color: var(--white);
          font-weight: 500;
        }
        h3{
            font-size: 18px;
            font-weight: 600;
            color: var(--white);
            padding-bottom: 15px;
            margin-bottom: 15px;
            position: relative;
            &:after{
               position: absolute;
               content: "";
               bottom: 0;
               left: 50%;
               height: 1px;
               width: 80px;
               background-color: var(--white);
               transform: translateX(-50%);
            }
        }
        p{
          font-size: 16px;
          line-height: 1.5;
          color: var(--white);
          margin-bottom: 0px;  
        }
    }
}

.hex {
    -webkit-clip-path: polygon(0% 50%, 25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%);
    -moz-clip-path: polygon(0% 50%, 25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%);
    padding: 50px 60px 35px;
    text-align: center;
    background:#00B2FF;
    &:after{
        position: absolute;
        content: "";
        background-color: var(--theme-blue-light);
        top: 2px;
        left: 2px;
        width: calc(100% - 4px);
        height: calc(100% - 4px);
        -webkit-clip-path: polygon(0% 50%, 25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%);
        -moz-clip-path: polygon(0% 50%, 25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%);
        z-index: -1;

    }
}

.hexo-design-area{
    position:relative;
    padding-bottom: 50px;
    .hexo-image-area{
        .hexo-1{
            position:absolute;
            bottom: 0;
            left: 300px;
        }
        .hexo-2{
            position:absolute;
            bottom: 50px;
            left: 50%;
            height: 100px;
        }
        .hexo-3{
            position:absolute;
            top: -150px;
            right: 300px;
            height: 70px;
        }
    }
}

