            color: var(--black2);
        }
        &:after , &:before{
            position: absolute;
            content: "";
            height: 25px;
            width: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
            right:0px;
            top: 0px;
            transition: .4s ease-in;

        }
        &:after{
            background-color: #DADADA;
        }
        &:before{
            content: "\ea99";
            font-family: IcoFont;
            font-size: 16px;
            color: var(--black);
            transform: rotateX(0deg);
            z-index: 1;

        }
    }
    .accordion-body{
        display: none;
    }
    &.active{
        background: #FFFFFF;
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.07);
       .accordion-head{
           &:after{
             background-color: var(--theme-blue-light); 
           }
           &:before{
             transform: rotateX(180deg); 
             color: var(--white);
           }
       } 
    }
    .accordion-body{
        padding: 20px 0px 0px;
        p{
            font-family: var(--ff-secondary);
            font-size: 16px;
            margin-bottom: 15px;   
        }
        ul{
            li{
                margin-bottom: 15px;
                padding-left: 15px;
                position: relative;
                &:after{
                    position: absolute;
                    content: "";
                    height: 6px;
                    width: 6px;
                    border-radius: 50%;
                    background-color: #555555;
                    top: 8px;
                    left: 0;
                }
                &:last-child{
                   margin-bottom: 0px;
                } 
            }
        }
    }
}
